<template>
    <v-btn  :disabled="disabled" text small @click.prevent="nav" :class="[header.class]">
        <v-icon small  >mdi-link-variant</v-icon>
        <span class="subtitle-2 font-weight-bold">
          
            {{ format($getNestedValue(item, header.value) || 0)  }} 
        </span>
    </v-btn>
 
</template>
<script>


export default {
    name:'TableFieldSparkline',
    props: {
     
        disabled: Boolean,
        path: String,
        query: Object,
        parent: Object,
        traget: {
            type: String,
            default: '_blank'
        },
        item: {
            type: Object,
        },
        header: {
            type: Object
        }
    },
    computed: {
        getQuery () {
           
            const temp = {}
            Object.keys(this.query).map(key => {
                temp[key] = typeof this.query[key] == 'function' ? this.query[key](this.parent, this.item) :  this.query[key] 
            })
            return temp

        }
    },
    methods: {
        format (value) {
            if (this.$options.filters[this.header.format]) { 
                return this.$options.filters[this.header.format](value)
            }
            return value
        },
        nav () {
            const { path, getQuery } = this
            const URL_Data = this.$router.resolve({path, query:getQuery});
            const a = document.createElement('a')
            a.target = this.target
            a.href = URL_Data.href
            a.click()
            a.remove()
            console.log(a);
        }
    } 
}
</script>

